.Gamediv{
    display: flex;
    flex-wrap: wrap;
    border-radius: 4px;
    padding: 5px;
    width: 90%;
    justify-content: space-around;
    font-family: "Courier New", Courier, monospace;
}
.gameDivCheldren{
width: 30%;
text-align: center;
transition: 0.5s;
margin-top: 3%;
}
.gameDivCheldren:hover {
  transform: scale(1.04);
}
.imgGamess{
  width: 100%;
  height: 350px;
  border-radius: 50px;
}
.likeIcon{
  display: inline-block;
  height: 30px;
  width: 30px;
}
.inputSearch{
  padding: 20px;
  border: 1px ;
  width: 300px;
  height: 20px;
  position: relative;
  margin-left: 530px;
  letter-spacing: 3px;
}
.buttonSearch{
  border: 1px;
  width: 150px;
  height: 40px;
  position: relative;
  letter-spacing: 2px;
}
  /*  */
  @media (max-width: 768px) {
    * {
      overflow-x: hidden;
    }
   
    .Gamediv{
      border-radius: 4px;
      padding: 5px;
      width: 90%;
  }
  .gameDivCheldren{
  width: 80%;
  text-align: center;
  transition: 0.5s;
  margin-top: 3%;
  }
  .gameDivCheldren:hover {
    transform: scale(1.04);
  }
  .imgGamess{
    width: 100%;
    height: 200px;
    border-radius: 50px;
  }
  .serch-cont {
    display: flex;
    justify-content: center;
  }
  .likeIcon{
    display: inline-block;
    height: 15px;
    width: 15px;
  }
  .inputSearch{
    
    margin: 0 ;
    width: 100px;
  }
  .buttonSearch{
    height: 45px;
    width: 25px;
    background-color: white;
  }
  }